
@import url('https://fonts.googleapis.com/css?family=Ubuntu&display=swap');
* {
  font-family : 'Ubuntu', sans-serif;
  text-align:center;
}
$shooting-time: 3000ms;
body { 
  background-color:black !important;  
}
.night {
  position: relative;
  width: 100%;
  height: 50%;
  transform: rotateZ(45deg);
  // animation: sky 200000ms linear infinite;
}
.slide-fwd-center {
	-webkit-animation: slide-fwd-center 1.5s cubic-bezier(0.250, 0.460, 0.450, 0.940) ;
	        animation: slide-fwd-center 1.5s cubic-bezier(0.250, 0.460, 0.450, 0.940) ;
}
.list-group-item:hover { 
  cursor:pointer;
}
.glass {
  background-color: rgba(255, 255, 255, .15) !important;  
  backdrop-filter: blur(5px);
 }
.shooting_star {
  position: absolute;
  left: 50%;
  top: 50%;
  // width: 100px;
  height: 2px;
  background: linear-gradient(-45deg, rgba(95, 145, 255, 1), rgba(0, 0, 255, 0));
  border-radius: 999px;
  filter: drop-shadow(0 0 6px rgba(105, 155, 255, 1));
  animation:
    tail $shooting-time ease-in-out infinite,
    shooting $shooting-time ease-in-out infinite;
  
  &::before {
    content: '';
    position: absolute;
    top: calc(20% - 1px);
    right: 0;
    // width: 30px;
    height: 2px;
    background: linear-gradient(-45deg, rgba(0, 0, 255, 0), rgba(95, 145, 255, 1), rgba(0, 0, 255, 0));
    transform: translateX(50%) rotateZ(45deg);
    border-radius: 100%;
    animation: shining $shooting-time ease-in-out infinite;
  }

  &::after {
    @extend .shooting_star::before;
    transform: translateX(50%) rotateZ(-45deg);
  }
  
  @for $i from 1 through 20 {
    &:nth-child(#{$i}) {
      $delay: random(9999) + 0ms;
      top: calc(50% - #{random(400) - 200px});
      left: calc(50% - #{random(300) + 0px});
      animation-delay: $delay;
      // opacity: random(50) / 100 + 0.5;
      
      &::before,
      &::after {
        animation-delay: $delay;
      }
    }
  }
}

@keyframes tail {
  0% {
    width: 0;
  }
  
  30% {
    width: 100px;
  }
  
  100% {
    width: 0;
  }
}

@keyframes shining {
  0% {
    width: 0;
  }
  
  50% {
    width: 30px;
  }
  
  100% {
    width: 0;
  }
}

@keyframes shooting {
  0% {
    transform: translateX(0);
  }
  
  100% {
    transform: translateX(300px);
  }
}

@keyframes sky {
  0% {
    transform: rotate(45deg);
  }
  
  100% {
    transform: rotate(45 + 360deg);
  }
}
 @-webkit-keyframes slide-fwd-center {
  0% {
    -webkit-transform: translateZ(0);
            transform: translateZ(0);
  }
  100% {
    -webkit-transform: translateZ(160px);
            transform: translateZ(160px);
  }
}
@keyframes slide-fwd-center {
  0% {
    -webkit-transform: translateZ(0);
            transform: translateZ(0);
  }
  100% {
    -webkit-transform: translateZ(160px);
            transform: translateZ(160px);
  }
}
.fade-in {
  animation: fadeIn ease 5s normal forwards;
  -webkit-animation: fadeIn ease 5s normal forwards;
  -moz-animation:   fadeIn ease 5s normal forwards ;
  -o-animation: fadeIn ease 5s normal forwards;
  -ms-animation: fadeIn ease 5s normal forwards;
}
@keyframes fadeIn {
  0% {
    opacity:0;
  }
  100% {
    opacity:1;
  }
}

@-moz-keyframes fadeIn {
  0% {
    opacity:0;
  }
  100% {
    opacity:1;
  }
}

@-webkit-keyframes fadeIn {
  0% {
    opacity:0;
  }
  100% {
    opacity:1;
  }
}

@-o-keyframes fadeIn {
  0% {
    opacity:0;
  }
  100% {
    opacity:1;
  }
}

@-ms-keyframes fadeIn {
  0% {
    opacity:0;
  }
  100% {
    opacity:1;
  }
}
.animated{
  -webkit-animation-duration:1s;
  animation-duration:1s;
  -webkit-animation-fill-mode:both;
  animation-fill-mode:both
}

@-webkit-keyframes fadeInLeft{
  0%{
      opacity:0;
      -webkit-transform:translateX(-200px);
      transform:translateX(-200px)}
  100%{
      opacity:1;
      -webkit-transform:translateX(0);
      transform:translateX(0)
  }
}
@keyframes fadeInLeft{
  0%{
      opacity:0;
      -webkit-transform:translateX(-200px);
      -ms-transform:translateX(-200px);
      transform:translateX(-200px)}
  100%{
      opacity:1;
      -webkit-transform:translateX(0);
      -ms-transform:translateX(0);
      transform:translateX(0)
  }
}
@-webkit-keyframes fadeInRight{
  0%{
      opacity:0;
      -webkit-transform:translateX(200px);
      transform:translateX(200px)}
  100%{
      opacity:1;
      -webkit-transform:translateX(0);
      transform:translateX(0)
  }
}
@keyframes fadeInRight{
  0%{
      opacity:0;
      -webkit-transform:translateX(-00px);
      -ms-transform:translateX(200px);
      transform:translateX(200px)}
  100%{
      opacity:1;
      -webkit-transform:translateX(0);
      -ms-transform:translateX(0);
      transform:translateX(0)
  }
}
.fadeInLeft{
  -webkit-animation-name:fadeInLeft;
  animation-name:fadeInLeft
}
.fadeInRight { 
  -webkit-animation-name: fadeInRight;
  animation-name: fadeInRight;
}
@media (min-width:720px){
  .item-2 {
